export default({
    data () {
        return {
        }
    },
    mounted () {
        $('.message .close')
            .on('click', function() {
                $(this)
                    .closest('.message')
                    .transition('fade')
                ;
            })
        ;
        $('.ui.basic.modal')
            .modal('show')
        ;
        $('#show_bar').on('click',function () {
            $('.ui.sidebar')
                .sidebar('toggle')
            ;
        })
        var
            $pageDimmer = $('.demo.page.dimmer'),
            $demo       = $('.dimmer.demo'),
            $showButton = $demo.find('.show.button'),
            $pageButton = $demo.find('.page.button'),
            $hideButton = $demo.find('.hide.button'),
            // alias
            handler
            ;
        // event handlers
        handler = {
            show: function() {
                $(this)
                    .closest('.demo')
                    .find('.segment')
                    .dimmer('show')
                ;
            },
            hide: function() {
                $(this)
                    .closest('.demo')
                    .find('.segment')
                    .dimmer('hide')
                ;
            },
            page: function() {
                $('body> #semantic_app')
                    .dimmer('show')
                ;
            }
        };
        $pageDimmer
            .dimmer()
        ;
        $pageButton
            .on('click', handler.page)
        ;
        $showButton
            .on('click', handler.show)
        ;
        $hideButton
            .on('click', handler.hide)
        ;
    }
})



